import { Button, Result } from 'antd';
import React from 'react';
import { Link } from 'react-router-dom';
import VerticalCenter from '../../templates/VerticalCenter';

export default function RegisterSuccess (props) {

    return (
        <VerticalCenter>
            <Result
                status="success"
                title="Cadastro realizado com sucesso"
                subTitle="Seu cadastro encontra-se em aprovao. Solicite ao administrador do sistema a ativao do seu cadastro."
                extra={[<Link to="/login"><Button>Voltar  tela inicial</Button></Link>]}
            />
        </VerticalCenter>
    );

}